@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Grandstander&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif,-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Recipe_recipe__3eeRW{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(37, 2, 9);
    background-color: white;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    width:40%;
    min-width: 400px;
    padding: 20px;
    margin: 20px;
}

.Recipe_image__jnmud{
    border-radius: 10px;
}
.App{
  min-height: 100vh;
  background-image: radial-gradient(#ffb199 30%, #ff0844 100%);
}

.logo{
  font-family: 'Grandstander', cursive;
  font-size: 150px;
  text-align: center;
  color: white;
  text-shadow: 2px 2px crimson;
  padding: 50px ;  
}

.search-form{
  min-height: 10vh;
  display:flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border:none;
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
}

.search-btn{
  background-color: crimson;
  border-radius: 10px;
  border: none;
  padding: 10px 30px;
  margin: 10px;
  color: white
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
